.navbar-default {
    background-image: -webkit-gradient(linear,left top, left bottom,color-stop(0, #fff),to(#c8f8f8));
    background-image: -webkit-linear-gradient(top,#fff 0,#c8f8f8 100%);
    background-image: -o-linear-gradient(top,#fff 0,#c8f8f8 100%);
    background-image: linear-gradient(to bottom,#fff 0,#c8f8f8 100%);
}



